.home{
	height: 1000px;
	width: 100%;
}
.banner{
	text-align: center;
	height: 600px;
	background: url(/static/img/f_home_banner_bg.8b9bf7f.jpg) no-repeat fixed left -100px/100%;
}
.banner h1{
	color: #fff;
	display: inline-block;
	font: 900 50px "\5FAE\8F6F\96C5\9ED1";
	padding: 5px 0;
	margin-top: 120px;
	border-bottom: 4px solid #fff;
}
