	color: #fff;
	font-size: 18px;
	margin-top: 10px;
}
.banner [class^="arrow"]{
	color: #fff;
	height: 80px;
	width: 80px;
	margin-top: 15px;
	font-size: 80px;
	line-height: 80px;
	border-radius: 50%;
	border: 2px solid #fff;
	background: transparent;
}
[class^="arrow"]:hover{
	background: rgba(200,200,200,.3);
}
.arrow-left{
	float: left;
	margin-left: 60px;
}
.arrow-right{
	float: right;
	margin-right: 60px;
}
.banner a{
	display: inline-block;
	color: #fff;
	height: 50px;
	width: 100px;
	font: 700 24px/50px "\5FAE\8F6F\96C5\9ED1";
	padding: 0 20px;
	margin-top: 40px;
	border: 2px solid #fff;
	border-radius:25px;
}
.banner a:hover{
	background: rgba(150,150,150,.3);
}
.banner [class^="circle"]{
	height: 20px;
	width: 20px;
	border: 2px solid #fff;
	border-radius: 50%;
	margin: 150px 10px 0;
	background: rgba(255,255,255,.3);
}
[class^="circle"]:hover{
	background: rgba(100,100,100,.5);
}
